import React from 'react';
import ReactDOM from 'react-dom';
import './globalStyle';
import App from './App';
import { HashRouter } from 'react-router-dom';
import registerServiceWorker from './registerServiceWorker';

ReactDOM.render(
    <HashRouter>
        <App />
    </HashRouter>,
    document.getElementById('root')
);
registerServiceWorker();



